                                                </span>
                                                <span v-if="bet.status=='loss'"
                                                      class=" badge badge-danger badge-pill">
                                                  {{ $trans('Tht bi') }}
                                                </span>
                                                <span v-if="bet.status=='success'"
                                                      class=" badge badge-warning badge-pill">
                                                  {{ $trans('Ch kt qu') }}
                                                </span>
                                            </td>
