                                </paginate>
                            </slot>
                            <slot v-else>
                                <div class="alert alert-warning">
                                    {{ $trans('Bn cha c lch s chi.') }}
                                </div>

                            </slot>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <modal v-if="addRule" v-bind:width="'900px'" v-bind:height="'unset'">
            <slot slot="body">
                <h5 class="mb-3">
                    {{ $trans('Quy tc') }}
                    <button @click="addRule = false" class="close">
                        <svg viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" stroke-width="2"
                             fill="none" stroke-linecap="round" stroke-linejoin="round" class="css-i6dzq1">
                            <line x1="18" y1="6" x2="6" y2="18"></line>
                            <line x1="6" y1="6" x2="18" y2="18"></line>
                        </svg>
                    </button>
                </h5>
                <table class="table table-striped c5m-rule">
                    <tbody>
                    <tr>
                        <td><h5>Gii thiu</h5></td>
