                            lch trnh.Nu khng, bn mt ti sn u t ca bn.
                        </td>
                    </tr>
                    <tr>
                        <td>Qui nh</td>
                        <td>Sau thi gian giao dch c nh, nhng thay i v gi ca ti sn c bn v gi thc thi
                            c chia thnh cc ty chn gi hoc t v ch c hai kt qu c th xy ra, c trong v
                            ngoi gi, khi chng ht hn. <br> Theo xu hng ca c s (gi c phiu, gi hi oi hoc
                            gi hng ha hoc ch s th trng chng khon) khi o hn ph hp vi d bo  xc nh
                            c li nhun hay khng, khi d on chnh xc hng ca gi ti sn hoc ch s c bn trong
                            mt thi gian nht nh, n nm trong gi, bn c th nhn c li sut xc nh v s tin
                            u t ban u, nu khng, n nm ngoi gi v s tin u t ban u s bng 0.Hoc ch
                            phc hi mt t l kh nh
                        </td>
                    </tr>
                    <tr>
                        <td>ng ca</td>
                        <td>Th trng ng ca t <br> 00:00 Mi th by n 12:00 mi th Hai khc <br> Ti sn khng
                            b nh hng
                        </td>
                    </tr>
                    <tr>
                        <td style="width: 105px;"><h5>Loi u t</h5></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td> LN, XUNG</td>
                        <td> Giao dch ny da trn t gi hi oi ti thi im mua v t gi hi oi hin ti vo
                            cui tun  tnh ton tng v gim, v ch s phn on l hai ch s cui cng. V d: Mua
                            li khi mua, t gi hi oi l 1.1152323, t gi hi oi hin ti l 1.1152339 vo cui
                            tun Hai yard cui cng khi mua l 23, hai yard cui cng khi tun ht hn l 39 Mua 23, ht
                            hn 39-for, mua Li nhun, nu khng, mua xung  mt
                        </td>
                    </tr>
                    </tbody>
                </table>
            </slot>
        </modal>

    </div>
</template>

<script>
// import TradingViewChart from "@/components/TradingView/TradingViewChart.vue";
import axios from "axios";
import moment from "moment";

export default {
    name: "Dashboard",
    // components: {TradingViewChart},
    data: function () {
        return {
            filterQuery: {
                page: 1,
                per_page: 10
            },
            process: false,
            isHide: false,
            errors: {},
            alert: {},
            alerts: {},
            currentTime: moment(new Date()).format('DD/MM/YYYY, HH:mm:ss '),
            bet_id_action: 0,
            timeCountdown: 60 - moment(new Date() || "").format("ss"),
            phienGiaodich: moment().add(1, "m").format("YYYYMMDDHHmm"),
            player: {
                nickname: '',
                money: 0
            },
            addFields: {
                command_bet: '',
                bet_id: '',
                price: 100000,
            },
            options: {
                'eth': 'ETH/USDT',
                'btc': 'BTC/USDT',
                'ada': 'ADA/USD',
                'doge': 'DOGE/USD',
                'dot': 'DOT/USD',
                'ltc': 'LTC/USD',
                'xrp': 'XRP/USD',
            },
            trade: 'eth',
            bets: {},
            addRule: false
        }
    },
    methods: {
        moment: function () {
            return moment();
        },
        filter: function () {
            this.filterQuery.page = 1;
            this.get_history_bet()
        },
        paginate: function (pageNum) {
            this.filterQuery.page = pageNum;
            this.get_history_bet();
        },
        onChange:function (event) {
            if(event.target.value !== 'eth') {
                this.$swal({
                    title: "ang b kha",
                    icon: "info",
                });
                this.trade = 'eth'
            }

        },
        get_info: function () {
            axios({url: this.$root.$data.api_url + '/api/player', method: 'GET'})
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        this.player = resp.data.results;
                    }
                })
                .catch(err => {
                    console.log(err)
                })
        },
        get_history_bet: function () {
            axios({url: this.$root.$data.api_url + '/api/history/command-bet', params: this.filterQuery, method: 'GET'})
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        this.bets = resp.data.results
                    } else {
                        this.alert = {
                            type: 'warning',
                            message: resp.data.message
                        }
                        this.errors = resp.data.errors || {};
                    }
                    this.process = false;
                })
                .catch(err => {
                    console.log(err)
                    this.process = false;
                })
        },
        formatPrice(value) {
            let val = (value/1).toFixed(0).replace(',', '.')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        },
        noti_resulft: function () {
            if (this.bet_id_action === 0) {
                return;
            }
            axios({
                url: this.$root.$data.api_url + '/api/history/new-history',
                params: {bet_id: this.bet_id_action},
                method: 'GET'
            })
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        let sum = resp.data.sum;
                        sum = sum.replace(',', '');
                        if (parseInt(sum) > 1) {
                            this.$swal({
                                title: "Chc mng!",
                                text: "Chc mng bn  chin thng s tin ! +" + resp.data.sum + "VND",
                                icon: "success",
                            });
                        } else if (parseInt(sum) < 1) {
                            this.$swal({
                                title: "Tht bi!",
                                text: "Bn  b tr tin",
                                icon: "error",
                            });
                        }

                    }
                    this.bet_id_action = 0
                })
                .catch(err => {
                    console.log(err)
                    this.bet_id_action = 0
                })
        },
        actionBet: function () {
            if (this.process === false) {
                this.process = true;
                this.errors = {}
                this.alert = null
                this.addFields.bet_id = this.phienGiaodich
                this.addFields.point_start = this.timeCountdown
                let data = this.addFields

                if(this.addFields.command_bet === '') {
                    this.$swal({
                        text: "Vui lng chn 1 cch chi ( Ln / Xung ).",
                        icon: "info",
                    });
                    this.process = false;
                    return false;
                }
                if (this.timeCountdown < 15) {
                    this.$swal({
                        text: "Ht thi gian t cc.",
                        icon: "error",
                    });
                    this.process = false;
                    return false;
                }
                if (parseInt(this.addFields.price) > parseInt(this.player.money)) {
                    this.$swal({
                        text: "S d ca bn khng   thc hin giao dch.",
                        icon: "error",
                    });
                    this.process = false;
                    return false;
                }
                if (parseInt(this.addFields.price) === 0) {
                    this.$swal({
                        text: "Bn cha chn s tin cc.",
                        icon: "error",
                    });
                    this.process = false;
                    return false;
                }
                let textBet = "Ln";
                if(this.addFields.command_bet == 'down'){
                    textBet = "Xung";
                }
                this.$swal.fire({
                    html: "<h2>Xc nhn giao dch</h2>" +
                        "<table class='table table-striped'>" +
                        "<tr><td>Th trng DG</td><td>ETH / USDT</td></tr>" +
                        "<tr><td>Xu hng u t</td><td>"+ textBet +"</td></tr>" +
                        "<tr><td>Thng nhn</td><td>90%</td></tr></tr>" +
                        "<tr><td>Phin giao dch</td><td>"+ this.addFields.bet_id +"</td></tr>" +
                        "<tr><td>S tin</td><td>"+ this.formatPrice(this.addFields.price) +" VND</td></tr>" +
                        "</table>",
                    showCancelButton: true,
                }).then((result) => {
                    if(result.isConfirmed){
                        axios({url: this.$root.$data.api_url + '/api/history/command-bet', data: data, method: 'POST'})
                            .then(resp => {
                                if (resp.data.error_code === 0) {
                                    this.get_info();
                                    this.get_history_bet()
                                    this.bet_id_action = resp.data.results.bet_id
                                    this.$swal({
                                        text: "t lnh thnh  vi M DG: " + resp.data.results.id + ", S tin : " + this.formatPrice(resp.data.results.price) +" VND, " + textBet,
                                        icon: "success",
                                    });
                                } else {
                                    this.$swal({
                                        text: "t lnh thnh khng thnh cng. ",
                                        icon: "error",
                                    });
                                }
                                this.process = false;
                            })
                            .catch(err => {
                                console.log(err)
                                this.process = false;
                            })
                    }
                    this.process = false;
                    this.addFields = {
                        command_bet: '',
                        bet_id: '',
                        price: 100000,
                    }
                })
            }
        },
        get_rule: function () {
            this.addRule = true
        }
    },
    created() {
        this.get_history_bet()
        this.get_info()
    },
    mounted: function () {
        window.setInterval(() => {
            this.currentTime = moment(new Date()).format('DD/MM/YYYY, HH:mm:ss ');
            this.timeCountdown = this.timeCountdown - 1
            if (this.timeCountdown === 0 || this.timeCountdown === 59 || this.timeCountdown === 58) {
                this.timeCountdown = 60 - moment(new Date() || "").format("ss");
                this.phienGiaodich = moment().add(1, "m").format("YYYYMMDDHHmm");
                this.get_info()
                this.noti_resulft()
                this.get_history_bet()
                this.alerts = {}
                this.alert = {}
                this.addFields = {
                    command_bet: '',
                    bet_id: '',
                    price: 100000,
                }
            }
        }, 1000)
    },
}
</script>

<style scoped>
.bo5c-actions {
    margin-top: 30px;
}

.bo5c-bet {
    margin-top: 30px;
}

.bo5c-title {
    font-size: 16px;
}

.c5m-time {
    font-size: 13px;
    margin: 10px 0;
}

.c5m-time span {
    padding: 10px;
    margin: 10px 0;
}

.radio-btn-group {
    display: flex;
    justify-content: center;
}

.radio-btn-group .radio {
    margin: 0 0.25rem;
    width: 100%;
}

.radio-btn-group .radio label {
    background: #fff;
    border: 1px solid #ddd;
    padding: 1.5rem 1.25rem;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    width: 100%;
    color: #444;
    transition: box-shadow 400ms ease;
}
.radio-btn-group .btn-up.radio label{
    border-color: #2196F3;
}
.radio-btn-group .btn-down.radio label{
    border-color: #dc3545;
}
.radio-btn-group .radio label:hover {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.radio-btn-group .radio input[type=radio] {
    display: none;
}

.radio-btn-group .btn-up.radio input[type=radio]:checked + label {
    background: #2196F3;
    color: #fff;
    border-color: #2196F3;
    width: 100%;
    text-align: center;
}
.radio-btn-group .btn-down.radio input[type=radio]:checked + label {
